// app/servicios/branding/page.tsx
import ServicePage from "@/components/ServicePage";
import type { Metadata } from "next";

export const metadata: Metadata = {
  title: "Diseño y Branding | Nodeva",
  description:
    "Construimos marcas memorables: logotipos, identidad visual, manuales de marca y piezas gráficas que conectan con tu audiencia.",
};

export default function BrandingPage() {
  return (
    <ServicePage
      title="Diseño & Branding"
      subtitle="Logotipo • Identidad • Piezas gráficas"
      intro="Tu marca es la primera impresión que das. En Nodeva diseñamos identidades visuales únicas, consistentes y estratégicas que comunican quién eres y hacia dónde vas."
      bullets={[
        { text: "Logotipos profesionales y memorables" },
        { text: "Identidad visual adaptable a todos los medios" },
        { text: "Manuales de marca claros y fáciles de aplicar" },
        { text: "Diseños gráficos para redes y materiales impresos" },
      ]}
      heroImage="/hero-branding.jpg" // cámbiala por una imagen tuya en /public
      sections={[
        {
          id: "logotipo",
          title: "Logotipo e Identidad",
          content: (
            <>
              <p>
                Creamos logotipos alineados a tus valores y objetivos. Cada
                propuesta se entrega con versiones escalables, monocromáticas y
                adaptadas a distintos usos.
              </p>
            </>
          ),
        },
        {
          id: "manual",
          title: "Manual de Marca",
          content: (
            <>
              <p>
                Documentamos la construcción de tu identidad: tipografías,
                paleta de colores, usos correctos/incorrectos, aplicaciones en
                digital e impreso. Tu equipo siempre sabrá cómo aplicar la marca.
              </p>
            </>
          ),
        },
        {
          id: "piezas",
          title: "Piezas Gráficas",
          content: (
            <>
              <p>
                Diseñamos materiales gráficos coherentes: posts para redes,
                banners, presentaciones, flyers, packaging y más.
              </p>
            </>
          ),
        },
      ]}
      cta={{
        title: "¿Listo para dar vida a tu marca?",
        text: "Desde un logotipo hasta un manual completo, trabajamos contigo para que tu identidad sea clara y reconocible.",
        buttonText: "Solicitar propuesta",
        href: "/contacto",
      }}
      faqs={[
        {
          q: "¿Qué entregables incluyen sus paquetes de logotipo?",
          a: "Archivos editables, versiones PNG/JPG, variantes monocromáticas y adaptaciones para redes sociales.",
        },
        {
          q: "¿Pueden rediseñar una marca ya existente?",
          a: "Sí, hacemos auditoría visual y proponemos un refresh o rebranding según el caso.",
        },
      ]}
    />
  );
}
